import _ from 'lodash';
import React, { Component } from 'react';
import PropTypes from 'prop-types';

// Import Insurer Logos
import MultiCompareView from '../Templates/MultiCompareView/MultiCompareView';

import LifeEditDetail from './LifeEditDetail';

import { ampTag } from '../../lib/tagFunctions';

class LifeChoosePlan extends Component {
  getAmpTags(selectedPlanData, planType) {
    try {
      let discount = null;
      _.map(selectedPlanData.info.discount, (data) => {
        discount = data.display;
      });
      if (_.isEmpty(discount)) {
        discount = '0%';
      }

      return ({
        partner: selectedPlanData.info.insurer_slug,
        price: selectedPlanData.policyInfo[planType].net_price,
        discount,
      });
    } catch (error) {
      console.log(`Life choose plan Buy now Amp tag failed: ${error.message}`);
    }

    return {};
  }

  render() {
    const { lifeFields } = this.props;

    return (
      <MultiCompareView
        {...this.props}
        category="life"
        form="life"
        editForm={{
          valuesToListen: [
            { value: 'gender', desc: 'Gender' },
            { value: 'smoker', desc: 'S.Status' },
            { value: 'birth_date', desc: 'Age' },
            { value: 'sum_insured', desc: 'Sum', extra: 'cost_simplify' },
            { value: 'sum_insured_selection', desc: 'Sum', extra: 'cost_simplify' },
            { value: 'premium_term', desc: 'Term' },
            { value: 'premium_whole', desc: 'Term' },
            { value: 'critical_illness', desc: 'CI Coverage' },
          ],
          conditions: {
            key: 'coverage_type',
            values: {
              term_life: ['gender', 'smoker', 'birth_date', 'sum_insured', 'premium_term', 'critical_illness'],
              whole_life: ['gender', 'smoker', 'birth_date', 'sum_insured_selection', 'premium_whole'],
            },
          },
          editDetail: <LifeEditDetail lifeFields={lifeFields} isPopUp />,
        }}
        buttonDesc="Select"
        forceFixedOnDesktop
        trackingTags={{
          default: (selectedPlanData) => {
            ampTag('LifeSelectA', this.getAmpTags(selectedPlanData, 'default'));
          },
        }}
      />
    );
  }
}

LifeChoosePlan.defaultProps = {
};

LifeChoosePlan.propTypes = {
  lifeFields: PropTypes.object.isRequired,
};

export default LifeChoosePlan;
